.help{
    padding: 75px 0 77px 0;
    background: #F6F5FC;
    position: relative;
    z-index: 1;
    &::before{
        content: '';
        position: absolute;
        top:0;
        right: 0;
        bottom: 0;
        width: 30%;
        height: auto;
        background: linear-gradient(134.79deg, rgba(5, 117, 230, 0.7) 0%, rgba(5, 117, 230, 0.7) 0%, rgba(2, 27, 121, 0.7) 99.63%);
        z-index: -1;
        
    }
    &__container{
        max-width: 1170px;
        margin: 0 auto;
    }
    &__wrapper{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__text{
        max-width: 585px;
    }
    &__title{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 47px;
        color: $text;
        margin-bottom: 20px;
    }
    &__descr{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 30px;
        color: $text;
        max-width: 500px;
        margin-bottom: 45px;
    }
    &__form{
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        input{
            border: none;
            font-family: 'Grava Display';
            font-style: normal;
            font-weight: 400;
            font-size: 20px;
            line-height: 25px;
            color: #D1D1CF;
            width: 200px;
            border-bottom: 1px solid #D1D1CF;
            padding-bottom: 5px;
            outline: none;
            background: transparent;
        }
    }
    &__submit{
        padding: 21px 60px;
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: $white;
        background: #4785EE;
        border-radius: 9px;
        border: 1px solid #4785EE;
        cursor: pointer;
        transition: all 0.3s;
        &:hover{
            background: $white;
            color: $main-color
        }
    }
    &__photo{ 
        width: auto;
        height: 378px;
        box-shadow: 8px 0px 20px rgba(117, 115, 111, 0.5);
        overflow: hidden;
        img{
            border-radius: 9px;
        }
    }
}