.hero{
    padding: 14px 0 116px 0;
    background:$bg;
    &__wrapper{
        max-width: 1238px;
        margin: 0 auto;
    }
    &__container{
        max-width: 1170px;
        margin: 0 auto;
    }
    &__menu{
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: $white;
        box-shadow: 8px 0px 20px rgba(117, 115, 111, 0.5);
        border-radius: 24px;
        padding: 14px 33px;
        margin-bottom: 90px;
    }
    &__logo{
        max-width: 178px;
        max-height: 42px;
        a{
            display: block;
            width: 100%;
            height: 42px;
        }
        svg{
            width: 100%;
            height: 42px;
        }
    }

    &__list{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 477px;
    }
    &__link{
        a {
            font-family: 'Grava';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            color: $text;
            &:hover{
                opacity: 0.7;
            }
        }
    }

    &__phone{
        a{
            font-family: 'Grava';
            font-style: normal;
            font-weight: 700;
            font-size: 24px;
            line-height: 30px;
            letter-spacing: 0.6px;
            color: $text;
            &:hover{
                opacity: 0.7;
            }
        }
    }
    &__subtel{
        margin-top: -5px;
        display: block;
        border: none;
        width: 100%;
        background-color: transparent;
        outline: none;
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: #4785EE;
        text-align: right;
        cursor: pointer;
    }
    .burger{
        display: none;
    }
    
    &__main{
        display: flex;
        justify-content: space-between;
        max-width: 1170px;
        align-items: center;
        margin: 0 auto;
    }
    &__title{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 47px;
        color: $white;
        max-width: 425px;
        margin-bottom: 25px;
    }
    &__subtitle{
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: rgba(255, 255, 255, 0.64);
        max-width: 387px;
        margin-bottom: 8px;
        &:nth-child(3){
            max-width: 309px;
            margin-bottom: 25px;
        }
    }
    &__button{
        padding: 21px 52px 21px 52px;
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: $white;
        background: transparent;
        border: 1px solid $white;
        border-radius: 9px;
        transition: all 0.5s;
        cursor: pointer;
        &:hover{
            color: $text;
            border: 1px solid $text;
        }
    }
    &__form{
        display: flex;
        flex-direction: column;
        background: #F5F4F2;
        border-radius: 24px;
        justify-content: flex-start;
        align-items: center;
        max-width: 394px;
        min-height: 470px;
    }
    &__adress{
        background: #FFFFFF;
        border-radius: 24px;
        padding: 17px 20px 20px 53px;
        margin-bottom: 6px;
        &_a{
            position: relative;
            input{
                background: transparent;
                font-family: 'Roboto';
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 19px;
                color: #151515;
                mix-blend-mode: normal;
                opacity: 0.6;
                border: none;
                border-bottom: 1px solid #D1D1CF;
                width: 100%;
                padding-bottom: 7px;
                margin-bottom: 21px;
                outline: none;
            }
            &:before{
                content: '';
                width: 24px;
                height: 24px;
                position: absolute;
                top: 0;
                left:-33px;
                background: $white;
                font-family: 'Roboto';
                font-style: normal;
                font-weight: 400;
                font-size: 10px;
                line-height: 12px;
                color: #4785EE;
                border: 1px solid #4785EE;
                border-radius: 24px;
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }

        &_b{
            position: relative;
            input{
                background: transparent;
                font-family: 'Roboto';
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 19px;
                color: #151515;
                opacity: 0.6;
                border: none;
                border-bottom: 1px solid #D1D1CF;
                width: 100%;
                padding-bottom: 7px;
                outline: none;
            }
            &::before{
                    content: 'B';
                    width: 24px;
                    height: 24px;
                    position: absolute;
                    top: 0;
                    left:-33px;
                    background: $white;
                    font-family: 'Roboto';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 10px;
                    line-height: 12px;
                    color: #4785EE;
                    border: 1px solid #4785EE;
                    border-radius: 24px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
            }
        }
    }

    &__car{
        background: $white;
        border-radius: 24px;
        padding: 20px 23px 20px 20px;
        margin-bottom: 6px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &s{
            width: 84px;
            height: 90px;
            border-radius: 12px;  
        }
        input[type=radio]{
            display: none;
        }
        input[type=radio]:checked + label{
            .hero__radio{
                background: #F5F4F2;
            }
            .hero__weight{
                opacity: 1;
            }
        }
    }
    &__radio{
        padding: 9px 3px 9px 9px;
        cursor: pointer;
        border-radius: 12px;
        svg{
            width: 44px;
            height: 30px;
            use{
                width: 100%;
                height: 100%;
            }
        }
    }
    &__weight{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: $text;
        opacity: 0.64;
    }
    &__price{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        color: $text;
    }
    &__contats{
        padding: 18px 20px 20px 53px;
        margin-bottom: 6px;
        background: $white;
        border-radius: 24px;
    }
    &__name{
        input{
            background: transparent;
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #151515;
            mix-blend-mode: normal;
            opacity: 0.6;
            border: none;
            border-bottom: 1px solid #D1D1CF;
            width: 100%;
            padding-bottom: 7px;
            margin-bottom: 21px;
            outline: none;
        }
        position: relative;
        &:before{
            content: '';
            background-image: url('../img/sprites/hero-user.svg');
            background-repeat: no-repeat;
            background-size: cover;
            width: 15px;
            height: 18px;
            position: absolute;
            top: 3px;
            left:-28px;
        }
    }
    &__telphone{
        input{
            background: transparent;
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #151515;
            mix-blend-mode: normal;
            opacity: 0.6;
            border: none;
            border-bottom: 1px solid #D1D1CF;
            width: 100%;
            padding-bottom: 7px;
            outline: none;
        }
        position: relative;
        &:before{
            content: '';
            background-image: url('../img/sprites/hero-telephone.svg');
            background-repeat: no-repeat;
            background-size: cover;
            width: 20px;
            height: 20px;
            position: absolute;
            top: 2px;
            left:-29px;
        }
    }
    &__submit{
        padding: 20px;
        background: #FFFFFF;
        border-radius: 24px;
        button{
            font-family: 'Grava Display';
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 20px;
            color: $white;
            background: #4785EE;
            border-radius: 9px;
            width: 354px;
            height: 57px;
            outline: none;
            border: none;
            cursor: pointer;
            transition: all 0.3s;
            &:hover{
                opacity: 0.7;
            }
            span{
                display: block;
                font-family: 'Grava Display';
                font-style: normal;
                font-weight: 400;
                font-size: 12px;
                line-height: 15px;
                color: #FFFFFF;
            }
        }
    }
}

