.reviews{
    padding: 90px 0 140px 0;
    background-color: $white;
    
    &__container{
        max-width: 1170px;
        margin: 0 auto;
        position: relative;
    }
    &__title{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 47px;
        color: $text;
        margin-bottom: 15px;
        text-align: center;
    }
    &__subtitle{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 30px;
        text-align: center;
        color: #191919;
        opacity: 0.64;
        max-width: 350px;
        margin: 0 auto;
        margin-bottom: 60px;
        text-align: center;
    }
    .swiper {
        width: 732px;
        height: 375px;
    }

    &__descr{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        text-align: center;
        color: $text;
        opacity: 0.64;
        max-width: 500px;
        margin: 0 auto;
        margin-bottom: 65px;
    }
    &__author{
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 258px;
        margin:0 auto;
    }
    &__img{
        width: 70px;
        height: 70px;
        border-radius: 24px;
        overflow: hidden;
    }
    &__name{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 25px;
        color: $text;
        margin-bottom: 7px;
    }
    &__whatsapp{
        padding-left: 34px;
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        color: #4CAF50;
        position: relative;
        &::before{
            content: '';
            width: 24px;
            height: 24px;
            position: absolute;
            top:0;
            left:0;
            background-image: url('../img/sprites/reviews-whatsapp.svg');
        }
    }
    .swiper-button-prev{
        width: 16px;
        height: 30px;
        transform: scale(-1, -1);
        left:216px;
        top:43%;
        svg{
            width: 16px;
            height: 30px;
            use{
                fill: #4785EE;
            }
        }
        &::after{
            display: none;
        }
    }
    .swiper-button-next{
        width: 16px;
        height: 30px;
        left: auto;
        right: 216px;
        top:43%;
        svg{
            width: 16px;
            height: 30px;
            use{
                fill: #4785EE;
            }
        }
        &::after{
            display: none;
        }
    }

    &__button{
        padding: 13px 52px 10px 52px;
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: $white;
        background: $main-color;
        border-radius: 9px;
        border: 1px solid $main-color;
        display: block;
        margin: 0 auto;
        margin-top: 80px;
        transition: all 0.3s;
        cursor: pointer;
        span{
            display: block;
            font-size: 12px;
            line-height: 15px;
        }
        &:hover{
            color:$main-color;
            background: $white;
        }
    }
    &__mobile{
        display: none;
    }
}