.comfort{
    background-color: $white;
    padding: 100px 0 158px 0;
    &__container{
        max-width: 1170px;
        margin: 0 auto;
    }
    &__title{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 47px;
        color: $text;
        margin-bottom: 51px;
    }
    &__wrapper{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-start;
    }
    &__block{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        max-width: 311px;
        &:nth-child(-n+3){
            margin-bottom: 60px;
        }
    }
    &__svg{
        width: 30px;
        height: 30px;
        margin-bottom: 20px;
    }
    &__text{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 25px;
        color: $text;
        margin-bottom: 9px;
    }
    &__descr{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: #191919;
        span{
            color:#4785EE;
        }
    }
}