@media (max-width:1200px) {
    .document{
        &__container{
            max-width: 960px;
        }
    }
}

@media (max-width:1024px) {
    .document{
        &__container{
            max-width: 720px;
        }
        &__wrapper{
            flex-direction: column;
            justify-content: flex-start;
        }
        &__title{
            margin-bottom: 35px;
            max-width: 100%;
        }
    }
}

@media (max-width:768px) {
    .document{
        &__container{
            max-width: 540px;
        }
        &__title{
            font-family: 'Grava';
            font-style: normal;
            font-weight: 700;
            font-size: 27px;
            line-height: 36px;
            color: #191919;
        }
        &__wrapper{
            justify-content: center;
            align-items: center;
        }
    }
}

@media (max-width:576px) {
    .document{
        padding: 50px 0 70px 0;
        &__container{
            max-width: 100%;
            padding: 0 15px;
        }
        &__descr{
            min-width: 0;
            width: 100%;
        }
        &__title{
            font-size: 26px;
            line-height: 36px;
        }
        &__buttons{
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
        &__form{
            min-width: 345px;
            margin-bottom:20px;
        }
        &__download{
            min-width: 345px;
        }
    }
}

@media (max-width:374px) {
    .document{
        &__form{
            min-width: 300px;
        }
        &__download{
            min-width: 300px;
        }
    }
}