@media (max-width: 1200px) {
    .response{
        &__container{
            max-width: 960px;
        }
    }
}

@media (max-width: 1024px) {
    .response{
        &__container{
            max-width: 720px;
        }
    }
}

@media (max-width: 768px) {
    .response{
        padding: 50px 0;
        &__container{
            max-width: 540px;
        }
        &__title{
            font-size: 27px;
            line-height: 26px;
            margin-bottom: 35px;
        }
    }
}

@media (max-width: 576px) {
    .response{
        &__container{
            max-width: 100%;
            padding: 0 15px;
        }
    }
}





