@media (min-width: 1025px) {
    .mobile-menu{
        display: none;
    }
}
@media (max-width: 1024px) {
    .mobile-menu{
        width: 100%;
        background: linear-gradient(134.79deg, rgba(5, 117, 230, 0.7) 0%, rgba(5, 117, 230, 0.7) 0%, rgba(2, 27, 121, 0.7) 99.63%);
        padding-bottom: 105px;
        position: fixed;
        top:-200%;
        left:0;
        transition: all 0.5s;
        z-index: -10;
        &__active{
            transition: all 0.5s;
            top:0;
            z-index: 10;
            height: 100vh;
        }
        &__wrapper{
            background-color: $white;
            border-bottom-left-radius: 24px;
            border-bottom-right-radius: 24px;
            padding-right: 30px;
            padding-left: 30px;
            padding-top: 13px;
            padding-bottom: 302px;
            
        }
        &__header{
            display: flex;
            justify-content: space-around;
            align-items: center;
            margin-bottom: 46px;
        }
        &__logo{
            a{
                display: block;
                width: 155px;
                height: 40px;
                svg{
                    width: 155px;
                    height: 40px;
                }
            }
        }
        &__tel{
            font-family: 'Grava';
            font-style: normal;
            font-weight: 700;
            font-size: 20px;
            line-height: 20px;
            letter-spacing: 0.6px;
            color: #191919;
        }
        &__close{
            width: 50px;
            height: 15px;
            cursor: pointer;
            transform: translate(-2px, 10px);
            span{
                &:nth-child(1){
                    display: block;
                    width: 35px;
                    height: 4px;
                    background: #4785EE;
                    border-radius: 5px;
                    transform: rotate(-45deg);
                }
                &:nth-child(2){
                    display: block;
                    width: 35px;
                    height: 4px;
                    background: #4785EE;
                    border-radius: 5px;
                    transform: rotate(45deg) translate(-3px, -2px);
                }
            }
        }
        &__list{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
        &__link{
            a{
                display: block;
                font-family: 'Grava';
                font-style: normal;
                font-weight: 500;
                font-size: 24px;
                line-height: 25px;
                color: #191919;
                
            }
            &:not(:last-child){
                margin-bottom: 40px;
            }
        }
    }
}
