.quiz{
    background: #F6F5FC;
    padding: 90px 0 145px 0;
    &__container{
        max-width: 760px;
        margin: 0 auto;
    }
    &__title{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 47px;
        text-align: center;
        color: $text;
        margin: 0 auto;
        margin-bottom: 15px;
        text-align: center;
        max-width: 434px;
    }
    &__subtitle{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 30px;
        text-align: center;
        color: $text;
        mix-blend-mode: normal;
        opacity: 0.64;
        max-width: 434px;
        margin: 0 auto;
        margin-bottom: 60px;
    }
    &__form{
        background: #FFFFFF;
        box-shadow: 8px 0px 20px rgba(117, 115, 111, 0.5);
        border-radius: 24px;
        max-width: 100%;
        margin: 0 auto;
        padding: 58px 50px 50px 50px;
    }
    &__package{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 30px;
        color: $text;
        margin-bottom: 25px;
        form{
            display: flex;
            flex-direction: column;   
        }   
    }
    &__flex{
        display: flex;
        align-items: flex-start;
        position: relative;
        &:not(:last-child){
            margin-bottom: 17px;
        }
    }
    &__checkbox{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 35px;
        color: $text;
    }
    &__wrapper{
        display: flex;
        justify-content: flex-end;
        align-items: center;
        .current{
            font-family: 'Grava';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 35px;
            letter-spacing: 2px;
            color: rgba(25, 25, 25, 0.6);
            mix-blend-mode: normal;
            margin-right: 25%;
            position: relative;
            &:before{
                content: '';
                position: absolute;
                top:0;
                right: -44px;
                width: 24px;
                height: 24px;
                background: #FFFFFF;
                border: 3px solid #4785EE;
                border-radius: 100%;
                opacity: 1;
            }
        }
    }

    &__button{
        background: $main-color;
        border-radius: 9px;
        transition: all 0.3s;
        padding: 21px 60px 21px 60px;
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: #FFFFFF;
        cursor: pointer;
        &:hover{
            opacity: 0.7;
        }
    }



    @supports(-webkit-appearance: none) or (-moz-appearance: none) {
        input[type=checkbox]{
          --active: #275EFE;
          --active-inner: #fff;
          --focus: 2px rgba(39, 94, 254, .3);
          --border: #4785EE;
          --border-hover: #275EFE;
          --background: #F6F5FC;
          --disabled: #F6F8FF;
          --disabled-inner: #E1E6F9;
          -webkit-appearance: none;
          -moz-appearance: none;
          width: 18px;
          height: 21px;
          outline: none;
          display: inline-block;
          vertical-align: top;
          position: relative;
          margin: 0;
          margin-right: 12px;
          cursor: pointer;
          border: 1px solid var(--bc, var(--border));
          background: var(--b, var(--background));
          transition: background .3s, border-color .3s, box-shadow .2s;
          &:after {
            content: '';
            display: block;
            left: 0;
            top: 0;
            position: absolute;
            transition: transform var(--d-t, .3s) var(--d-t-e, ease), opacity var(--d-o, .2s);
          }
          &:checked {
            --b: var(--active);
            --bc: var(--active);
            --d-o: .3s;
            --d-t: .6s;
            --d-t-e: cubic-bezier(.2, .85, .32, 1.2);
          }
          &:disabled {
            --b: var(--disabled);
            cursor: not-allowed;
            opacity: .9;
            &:checked {
              --b: var(--disabled-inner);
              --bc: var(--border);
            }
            & + label {
              cursor: not-allowed;
            }
          }
          &:hover {
            &:not(:checked) {
              &:not(:disabled) {
                --bc: var(--border-hover);
              }
            }
          }
          &:focus {
            box-shadow: 0 0 0 var(--focus);
          }
          &:not(.switch) {
            width: 21px;
            &:after {
              opacity: var(--o, 0);
            }
            &:checked {
              --o: 1;
            }
          }
          & + label {
            font-size: 16px;
            line-height: 21px;
            display: inline-block;
            vertical-align: top;
            cursor: pointer;
            margin-left: 4px;
          }
        }
        input[type=checkbox] {
          &:not(.switch) {
            border-radius: 0px;
            &:after {
              width: 5px;
              height: 9px;
              border: 2px solid var(--active-inner);
              border-top: 0;
              border-left: 0;
              left: 6px;
              top: 3px;
              transform: rotate(var(--r, 20deg));
            }
            &:checked {
              --r: 43deg;
            }
          }
          &.switch {
            width: 38px;
            border-radius: 11px;
            &:after {
              left: 2px;
              top: 2px;
              border-radius: 50%;
              width: 15px;
              height: 15px;
              background: var(--ab, var(--border));
              transform: translateX(var(--x, 0));
            }
            &:checked {
              --ab: var(--active-inner);
              --x: 17px;
            }
            &:disabled {
              &:not(:checked) {
                &:after {
                  opacity: .6;
                }
              }
            }
          }
        }
        
      }
      
     
      
      
      
    
}