.footer{
    padding: 71px 0 160px 0;
    background: #222222;
    &__container{
        max-width: 1170px;
        margin: 0 auto;
    }
    &__contacts{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-bottom: 46px;
        svg{
            width: 175px;
            height: 42px;
            use{
                width: 100%;
                height: 100%;
            }
        }
    }
    &__phone{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        a{
            font-family: 'Grava';
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 25px;
            color: $white;
            transition: all 0.3s;
            &:hover{
                opacity: 0.7;
            }
        }
        span{
            font-family: 'Roboto', sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 14px;
            color: $white;
            opacity: 0.64;
        }
    }
    &__email{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        a{
            font-family: 'Grava';
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 25px;
            color: $white;
            transition: all 0.3s;
            &:hover{
                opacity: 0.7;
            }
        }
        span{
            font-family: 'Roboto', sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 14px;
            color: $white;
            opacity: 0.64;
        }
    }
    &__button{
        padding: 21px 60px;
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: $white;
        background: $main-color;
        border: 1px solid $main-color;
        border-radius: 9px;
        outline: none;
        cursor: pointer;
        transition: all 0.3s;
        &:hover{
            color: $main-color;
            background: transparent;
        }
    }

    &__author{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
    &__data{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        span{
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
            color: $white;
            opacity: 0.64;
            &:nth-child(1){
                margin-bottom: 8px;
            }
        }
    }

    &__policy{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-end;
        a{
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
            color: $white;
            opacity: 0.64;
            transition: all 0.3s;
            &:hover{
                opacity: 1;
            }
            &:nth-child(1){
                margin-bottom: 8px;
            }
        }
    }
}