@media (max-width: 1200px) {
    .help{
        &__container{
            max-width: 960px;
        }
        &__photo{
            display: none;
        }
    }
}

@media (max-width: 1024px) {
    .help{
        &__container{
            max-width: 720px;
        }
        
        &::before{
            display: none;
        }
    }
}

@media (max-width: 768px) {
    .help{
        &__container{
            max-width: 540px;
        }
        &__title{
            font-size: 27px;
            line-height: 36px;
        }
    }
}

@media (max-width: 576px) {
    .help{
        padding: 50px 0 70px 0;
        &__container{
            max-width: 100%;
            padding: 0 15px;
        }
        &__title{
            max-width: 314px;
        }
        &__form{
            flex-direction: column;
            justify-content: center;
            align-items: center;
            input{
                width: 100%;
                margin-bottom: 25px;
            }
        }
        &__submit{
            width: 100%;
        }
    }
}