.document{
    background-color: $white;
    padding: 90px 0 140px 0;
    &__container{
        max-width: 1170px;
        margin: 0 auto;
    }
    &__wrapper{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
    &__title{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 47px;
        color: $text;
        max-width: 532px;
    }
    &__descr{
        min-width: 537px;
    }
    &__suplist{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 30px;
        color: $text;
        opacity: 0.64;
        max-width: 500px;
        margin-bottom: 20px;
    }
    &__item{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 30px;
        color: $text;
        padding-left: 27px;
        position: relative;
        &:not(:last-child){
            margin-bottom: 2px;
        }
        &::before{
            content: url('../img/sprites/document-check.svg');
            width: 15px;
            height: 11px;
            position: absolute;
            top:0;
            left: 0px;
        }
    }
    &__buttons{
        margin-top: 42px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__form{
        padding: 21px 60px;
        background: $main-color;
        border: 1px solid $main-color;
        border-radius: 9px;
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: #FFFFFF;
        outline: none;
        cursor: pointer;
        transition: all 0.3s;
        &:hover{
            background: $white;
            color: $main-color;
            border: 1px solid $main-color;
        }
    }
    &__download{
        padding: 13px 34px 10px 34px;
        background: $white;
        border: 1px solid $main-color;
        border-radius: 9px;
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: $main-color;
        outline: none;
        cursor: pointer;
        transition: all 0.3s;
        &:hover{
            background: $main-color;
            color: $white;
            border: 1px solid $main-color;
            span{
                color: $white;
            }
        }
        span{
            display: block;
            font-size: 12px;
        }
    }
}