.free{
    padding: 90px 0 103px 0;
    background: $bg;
    &__container{
        max-width: 1170px;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    &__title{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 47px;
        color: $white;
        margin-bottom: 50px;
    }
    &__form{
        display: flex;
        flex-direction: column;
        background: $white;
        border-radius: 24px;
        justify-content: flex-start;
        align-items: center;
        max-width: 444px;
        padding: 41px 25px 42px;
    }
    &__adress{
        background: $white;
        border-radius: 24px;
        padding: 17px 20px 20px 53px;
        margin-bottom: 6px;
        &_a{
            position: relative;
            input{
                background: transparent;
                font-family: 'Roboto';
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 19px;
                color: #151515;
                opacity: 0.6;
                border: none;
                border-bottom: 1px solid #D1D1CF;
                width: 100%;
                padding-bottom: 7px;
                margin-bottom: 21px;
                outline: none;
            }
            &:before{
                content: '';
                width: 24px;
                height: 24px;
                position: absolute;
                top: 0;
                left:-33px;
                background: $white;
                font-family: 'Roboto';
                font-style: normal;
                font-weight: 400;
                font-size: 10px;
                line-height: 12px;
                color: #4785EE;
                border: 1px solid #4785EE;
                border-radius: 24px;
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }

        &_b{
            position: relative;
            input{
                background: transparent;
                font-family: 'Roboto';
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 19px;
                color: #151515;
                opacity: 0.6;
                border: none;
                border-bottom: 1px solid #D1D1CF;
                width: 100%;
                padding-bottom: 7px;
                outline: none;
            }
            &::before{
                    content: 'B';
                    width: 24px;
                    height: 24px;
                    position: absolute;
                    top: 0;
                    left:-33px;
                    background: $white;
                    font-family: 'Roboto';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 10px;
                    line-height: 12px;
                    color: #4785EE;
                    border: 1px solid #4785EE;
                    border-radius: 24px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
            }
        }
    }

    &__car{
        background: $white;
        border-radius: 24px;
        padding: 20px 23px 20px 20px;
        margin-bottom: 6px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &s{
            width: 84px;
            height: 90px;
            border-radius: 12px;  
        }
        input[type=radio]{
            display: none;
        }
        input[type=radio]:checked + label{
            .free__radio{
                background: #F5F4F2;
            }
            .free__weight{
                opacity: 1;
            }
        }
    }
    &__radio{
        padding: 9px 3px 9px 9px;
        cursor: pointer;
        border-radius: 12px;
        svg{
            width: 44px;
            height: 30px;
            use{
                width: 100%;
                height: 100%;
            }
        }
    }
    &__weight{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: $text;
        opacity: 0.64;
    }
    &__price{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        color: $text;
    }
    &__contats{
        padding: 18px 20px 20px 53px;
        margin-bottom: 6px;
        background: $white;
        border-radius: 24px;
    }
    &__name{
        input{
            background: transparent;
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #151515;
            mix-blend-mode: normal;
            opacity: 0.6;
            border: none;
            border-bottom: 1px solid #D1D1CF;
            width: 100%;
            padding-bottom: 7px;
            margin-bottom: 21px;
            outline: none;
        }
        position: relative;
        &:before{
            content: '';
            background-image: url('../img/sprites/hero-user.svg');
            background-repeat: no-repeat;
            background-size: cover;
            width: 15px;
            height: 18px;
            position: absolute;
            top: 3px;
            left:-28px;
        }
    }
    &__telphone{
        input{
            background: transparent;
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #151515;
            mix-blend-mode: normal;
            opacity: 0.6;
            border: none;
            border-bottom: 1px solid #D1D1CF;
            width: 100%;
            padding-bottom: 7px;
            outline: none;
        }
        position: relative;
        &:before{
            content: '';
            background-image: url('../img/sprites/hero-telephone.svg');
            background-repeat: no-repeat;
            background-size: cover;
            width: 20px;
            height: 20px;
            position: absolute;
            top: 2px;
            left:-29px;
        }
    }
    &__submit{
        padding: 20px;
        background: #FFFFFF;
        border-radius: 24px;
        button{
            font-family: 'Grava Display';
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 20px;
            color: $white;
            background: #4785EE;
            border-radius: 9px;
            width: 354px;
            height: 57px;
            outline: none;
            border: none;
            cursor: pointer;
            transition: all 0.3s;
            &:hover{
                opacity: 0.7;
            }
        }
    }
}