@media (max-width:1200px) {
    .reviews{
        &__container{
            max-width: 960px;
        }
        .swiper-button-prev {
            left: 15%;
        }
        .swiper-button-next{
            right: 15%;
        }
    }
}

@media (max-width:1024px) {
    .reviews{
        &__container{
            max-width: 720px;
        }
        .swiper-button-prev {
            left: 5%;
        }
        .swiper-button-next{
            right: 5%;
        }
        .swiper{
            width: auto;
            height: auto;
        }
        &__subtitle{
            margin-bottom: 35px;
            text-align: center;
        }
    }
}

@media (max-width:768px) {
    .reviews{
        padding: 50px 0 70px 0;
        &__container{
            max-width: 540px;
        }
        &__title{
            font-size: 27px;
            line-height: 36px;
            margin-bottom: 15px;
        }
        &__descr{
            text-align: center;
            max-width: 295px;
            margin-bottom: 20px;
        }
        &__button{
            margin-top: 25px;
        }
    }
}

@media (max-width:576px) {
    .reviews{
        &__container{
            max-width: 100%;
            padding: 0 15px;
        }
        &__title{
            text-align: left;
            margin: 0 auto;
            margin-bottom: 15px;
        }
        &__subtitle{
            text-align: left;
            margin: 0;
            margin-bottom: 20px;
        }
        &__descr{
            text-align: left;
            margin: 0;
            margin-bottom: 20px;

        }
        .swiper-button-prev {
            display: none;
        }
        .swiper-button-next{
            top:48%;
        }
        &__author{
            margin: 0;
            margin-bottom: 25px;
        }
        &__button{
            display: none;
        }
        &__mobile{
            padding: 21px 108px;
            display: block;
            border: 1px solid #4785EE;
            border-radius: 9px;
            background: transparent;
            font-size: 16px;
            line-height: 20px;
            color: #4785EE;
        }
    }
}

@media (max-width:374px) {
    .reviews{
        &__mobile{
            padding: 21px 80px;
        }
    }
}