@media (max-width: 1200px) {
    .free{
        &__container{
            max-width: 960px;
        }
    }
}

@media (max-width: 1024px) {
    .free{
        &__container{
            max-width: 720px;
        }
    }
}

@media (max-width: 768px) {
    .free{
        padding: 50px 0 70px 0;
        &__container{
            max-width: 540px;
        }
        &__title{
            font-size: 27px;
            line-height: 36px;
            text-align: left;
            margin-bottom: 35px;
        }
    }
}

@media (max-width: 576px) {
    .free{
        overflow-x: hidden;
        &__container{
            max-width: 100%;
            padding: 0 15px;
        }
        &__form{
            padding: 17px 15px 20px;
            max-width: 375px;
            form{
                max-width: 375px;
            }
        }
        &__submit{
            padding: 10px;
        }
    }
}

@media (max-width:374px) {
    .free{
        &__form{
            max-width: 320px;
            form{
                max-width: 320px;
            }
        }
        &__adress{
            padding: 17px 10px 20px 40px;
        }
        &__car{
            padding: 20px 10px 20px 10px;
        }
        &__price{
            font-size: 12px;
        }
        &__submit{
            padding: 20px 5px 20px 5px;
            button{
                width: 310px;
            }
        }
        &__button{
            padding: 21px 18px 21px 18px;
        }
    }
}