.modal-info{
    background: rgba(71, 133, 238, 0.5);
    backdrop-filter: blur(4px);
    width: 100%;
    height: 100vh;
    transition: all 0.5s;
    inset: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    opacity: 0;
    z-index: -5;
    display: none;
    &__active{
        z-index: 15;
        opacity: 1;
        transition: all 0.5s;
        display: flex;
    }
    &__wrapper{
        padding: 40px 120px 34px 35px;
        max-width: 575px;
        background: $white;
        box-shadow: 8px 0px 20px rgba(117, 115, 111, 0.5);
        border-radius: 24px;
        position: relative;
    }
    &__title{
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 400;
        font-size: 24px;
        line-height: 30px;
        color: #1C1B1F;
        max-width: 420px;
        margin-bottom: 25px;
    }
    &__close{
        cursor: pointer;
        position: absolute;
        top:23px;
        right: 25px;
        transition: all 0.3s;
        &:hover{
            opacity: 0.7;
        }
        svg{
            width: 33px;
            height: 33px;
            use{
                width: 100%;
                height: 100%;
            }
        }
    }
    &__item{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 30px;
        color: #191919;
        margin-bottom: 5px;
        padding-left: 27px;
        position: relative;
        &::before{
            content: '';
            position: absolute;
            top:40%;
            left:0;
            width: 15px;
            height: 11px;
            background: url('../img/sprites/document-check.svg') center /cover no-repeat;
        }
    }
}