@media (max-width:1200px) {
    .step{
        &__container{
            max-width: 960px;
            margin: 0 auto;
        }
        &__wrapper{
            flex-wrap: wrap;
        }
        &__block{
            min-width: 360px;
            &:nth-child(3){
                margin-top: 35px;
            }
        }
    }
    
}

@media (max-width:1024px) {
    .step{
        padding: 50px 0 70px 0;
        &__container{
            max-width: 720px;
            margin: 0 auto;
        }
    }
    
}

@media (max-width:768px) {
    .step{
        &__container{
            max-width: 540px;
            margin: 0 auto;
        }
        &__wrapper{
            justify-content: flex-start;
        }
        &__block{
            min-width: 311px;
            max-width: 311px;
            &:not(:last-child){
                margin-bottom: 35px;
            }
            &:nth-child(3){
                margin-top: 0px;
            }
        }
    }
    
}

@media (max-width:576px) {
    .step{
        &__container{
            max-width: 100%;
            padding: 0 15px;
            margin: 0 auto;
        }
        &__wrapper{
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
        }
        &__block{
            min-width: 344px;
            max-width: 345px;
        }
    }
    
}

@media (max-width:374px) {
    .step{
        &__block{
            min-width: 100%;
        }
    }
}