.autopark{
    padding: 90px 0 145px 0;
    background-color: $white;
    &__container{
        max-width: 1170px;
        margin: 0 auto;
    }
    &__title{
        font-family: 'Grava';
        text-align: center;
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 47px;
        color: $text;
        margin-bottom: 45px;
    }
    &__tabs{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 auto;
        max-width: 768px;
        margin-bottom: 75px;
    }
    &__item{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 25px;
        color: $text;
        mix-blend-mode: normal;
        opacity: 0.64;
        transition: all 0.3s;
        cursor: pointer;
        border-bottom: 1px solid transparent;
        &:hover{
            opacity: 0.8;
        }
        &_active{
            opacity: 1;
            border-bottom: 1px solid #4785EE;
        }
    }
    &__block{
        display: none;
        transition: all 0.5s;
        &_active{
            display: flex;
            align-items: flex-start;
            justify-content: baseline;
            transition: all 0.5s;
        }
    }
    &__img{
        max-width: 525px;
        max-height: 308px;
        margin-right: 80px;
        img{
            width: 525px;
            height: 308px;
        }
    }
    &__text{
        max-width: 382px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
    }
    &__descr{
        min-width: 144px;
        &:nth-child(-n+4){
            margin-bottom: 20px;
        }
        &_title{
            font-family: 'Grava';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 20px;
            color: $text;
            opacity: 0.64;
            margin-bottom: 11px;
        }
        &_value{
            font-family: 'Grava';
            font-style: normal;
            font-weight: 400;
            font-size: 20px;
            line-height: 25px;
            color: $text;
        }
    }
    &__button{
        margin-top: 35px;
        display: flex;
        justify-content: space-between;
        min-width: 537px;
    }
    &__modal{
        padding: 21px 45px;
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: $main-color;
        border: 1px solid #4785EE;
        border-radius: 9px;
        background: $white;
        cursor: pointer;
        transition: all 0.3s;
        &:hover{
            color:$white;
            background: $main-color;
        }
    }
    &__form{
        padding: 21px 60px;
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: $white;
        border: 1px solid #4785EE;
        border-radius: 9px;
        background: $main-color;
        cursor: pointer;
        transition: all 0.3s;
        &:hover{
            color:$main-color;
            background: $white;
        }
    }
    &__whatsapp{
        margin: 0 auto;
        margin-top: 62px;
        max-width: 500px;
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 24px;
        text-align: center;
        color: #6C6C6C;
        a{  
            transition: all 0.3s;
            color: #4CAF50;
            &:hover{
                opacity: 0.7;
            }
        }
    }

    .fade{
        animation: autopark__block_active .5s cubic-bezier(0.6, 0, 0.18, 0.99)
    }
    @keyframes autopark__block_active{
        0%{
            opacity: 0;
        transform: scale(0.98);
        }
        
    100%{
        opacity: 1;
        transform: scale(1);
    }
        
    }
  
}