@media (max-width:1200px) {
    .hero{
        &__container{
            max-width: 960px;
        }
    }
}

@media (max-width:1024px) {
    .hero{
        &__container{
            max-width: 720px;
        }
        &__list{
            display: none;
        }
        .burger{
            display: flex;
            justify-content: flex-end;
            flex-direction: column;
            cursor: pointer;
            width: 50px;
            div{
                &:nth-child(1){
                    width: 50px;
                    height: 4px;
                    background: #4785EE;
                    border-radius: 5px;
                    margin-bottom: 7px;
                }
                &:nth-child(2){
                    width: 35px;
                    height: 4px;
                    background: #4785EE;
                    border-radius: 5px;
                }
            }
        }
        &__subtel{
            display: none;
        }
        &__main{
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
        }
        &__text{
            display: flex;
            flex-direction: column;
            order:2;
            justify-content: center;
            align-items: flex-start;
            align-self: flex-start;
        }
        &__title{
            font-size: 26px;
            line-height: 36px;
            margin-bottom: 20px;
            max-width: 100%;
        }
        &__subtitle{
            font-size: 16px;
            line-height: 21px;
            margin-bottom: 17px;
            max-width: 100%;
            &:nth-child(3){
                max-width: 100%;
                margin-bottom: 30px;
            }
        }
        &__form{
            margin-bottom: 35px;
        }
    }
}

@media (max-width:768px) {
    .hero{
        &__container{
            max-width: 540px;
        }
    }
}

@media (max-width:576px) {
    .hero{
        padding-top: 0;
        overflow-x: hidden;
        &__container{
            max-width: 100%;
        }
        &__menu{
            padding: 12px 15px;
            border-top-left-radius: 0px;
            border-top-right-radius: 0px;
            margin-bottom: 30px;
        }
        &__text{
            padding: 0 15px;
        }
        &__logo{
            width: 100px;
            height: 35px;
        }
        &__phone a{
            font-size: 16px;
            line-height: 20px;
        }
        &__form{
            max-width: 375px;
            min-width: 320px;
            align-self: center;
            form{
                max-width: 375px;
            }
        }
        &__button{
            max-width: 345px;
            padding: 21px 45px 21px 45px;
        }
    }
}
@media (max-width:374px) {
    .hero{
        &__phone{
            a{
                font-size: 14px;
            }    
        }
        &__form{
            form{
                max-width: 320px;
            }
        }
        &__adress{
            padding: 17px 10px 20px 40px;
        }
        &__car{
            padding: 20px 10px 20px 10px;
        }
        &__price{
            font-size: 12px;
        }
        &__submit{
            padding: 20px 5px 20px 5px;
            button{
                width: 310px;
            }
        }
        &__button{
            padding: 21px 18px 21px 18px;
        }
    }
}