.keys{
    padding: 14px 0 110px 0;
    background:$bg;
    &__wrapper{
        max-width: 1238px;
        margin: 0 auto;
    }
    &__menu{
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: $white;
        box-shadow: 8px 0px 20px rgba(117, 115, 111, 0.5);
        border-radius: 24px;
        padding: 14px 33px;
        margin-bottom: 90px;
    }
    &__logo{
        max-width: 178px;
        max-height: 42px;
        a{
            display: block;
            width: 100%;
            height: 42px;
        }
        svg{
            width: 100%;
            height: 42px;
        }
    }

    &__list{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 477px;
    }
    &__link{
        a {
            font-family: 'Grava';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            color: $text;
            &:hover{
                opacity: 0.7;
            }
        }
    }

    &__phone{
        a{
            font-family: 'Grava';
            font-style: normal;
            font-weight: 700;
            font-size: 24px;
            line-height: 30px;
            letter-spacing: 0.6px;
            color: $text;
            &:hover{
                opacity: 0.7;
            }
        }
    }
    &__subtel{
        margin-top: -5px;
        display: block;
        border: none;
        width: 100%;
        background-color: transparent;
        outline: none;
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: #4785EE;
        text-align: right;
        cursor: pointer;
    }
    .burger{
        display: none;
    }

    &__container{
        max-width: 1240px;
        margin: 0 auto;
    }
    &__block{
        background: #FFFFFF;
        backdrop-filter: blur(4px);
        border-radius: 20px;
        padding: 63px 0 75px;
    }
    &__title{
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 400;
        font-size: 48px;
        line-height: 55px;
        color: #1C1B1F;
        margin-bottom: 44px;
        padding-left: 35px;
        padding-right: 35px;
    }
    &__subtitle{
        display: block;
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 400;
        font-size: 24px;
        line-height: 30px;
        color: #1C1B1F;
        max-width: 830px;
        margin-bottom: 55px;
        padding-left: 35px;
        padding-right: 35px;
    }
    &__img{
        max-width: 810px;
        margin-bottom: 40px;
        padding-left: 35px;
    }
    &__descr{
        max-width: 810px;
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 24px;
        color: #1C1B1F;
        margin-bottom: 30px;
        padding-left: 35px;
        padding-right: 35px;
        &:nth-child(3){
            margin-bottom: 0;
        }
    }
    &__blockquote{
        padding: 80px 0 100px;
        background: #F5F5F5;
        width: 100%;
        padding-left: 35px;
        padding-right: 35px;
        margin-bottom: 55px;
        blockquote{
            font-family: 'Grava Display';
            font-style: normal;
            font-weight: 400;
            font-size: 36px;
            line-height: 48px;
            color: #4785EE;
            margin: 0;
            max-width: 945px;
        }
    }
}