.response{
    padding: 90px 0 140px 0;
    background-color: $white;
    &__container{
        max-width: 1170px;
        margin: 0 auto;
    }
    &__title{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 47px;
        color: $text;
        margin-bottom: 52px;
    }
    .accordion {
        width: 100%;
        overflow: hidden;
        background-color: transparent;
        &:not(:last-child){
            margin-bottom: 13px;
        }
        &__intro {
            position: relative;
            cursor: pointer;
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            border-bottom: 1px solid #DFDFDF;
            transition: 0.3s;
            svg{
                width: 20px;
                height: 20px;
            }
        }
        &__title{
            font-family: 'Grava';
            font-style: normal;
            font-weight: 600;
            font-size: 18px;
            line-height: 27px;
            color: #191919;
            width: 100%;
            padding-bottom: 18px;
            border-bottom: 0px solid #DFDFDF;
            transition: 0.3s;
        }
        &__descr{
            font-family: 'Grava';
            font-style: normal;
            font-weight: 400;
            font-size: 18px;
            line-height: 27px;
            color: $text;
            max-width: 694px;
        }
        &__content {
            max-height: 0;
            overflow: hidden;
            will-change: max-height;
            transition: all 0.25s ease-out;
            color: #f1f1f1;
            opacity: 0;
            padding-bottom: 18px;
        }
    
        &__active {
            .accordion__content {
                opacity: 1;
                border-bottom: 1px solid #DFDFDF;
                transition: 0.3s;
            }
            .accordion__intro{
                border-bottom: 0px;
                transition: all 0.3s;
            }
        }
    }
    
}