@media (max-width:1200px) {
    .keys{
        &__container{
            max-width: 960px;
        }
    }
}

@media (max-width:1024px) {
    .keys{
        &__container{
            max-width: 720px;
        }
        &__list{
            display: none;
        }
        .burger{
            display: flex;
            justify-content: flex-end;
            flex-direction: column;
            cursor: pointer;
            width: 50px;
            div{
                &:nth-child(1){
                    width: 50px;
                    height: 4px;
                    background: #4785EE;
                    border-radius: 5px;
                    margin-bottom: 7px;
                }
                &:nth-child(2){
                    width: 35px;
                    height: 4px;
                    background: #4785EE;
                    border-radius: 5px;
                }
            }
        }
        &__subtel{
            display: none;
        }
        &__img{
            max-width: 540px;
        }
    }
}

@media (max-width:768px) {
    .keys{
        &__block{
            padding-top: 45px;
            padding-bottom: 50px;
        }
        &__container{
            max-width: 540px;
        }
        &__title{
            font-size: 24px;
            line-height: 36px;
            margin-bottom: 20px;
            padding: 0 15px;
        }
        &__subtitle{
            font-size: 18px;
            padding: 0 15px;
        }
        &__img{
            max-width: 345px;
            padding: 0 15px;
        }
        &__descr{
            font-size: 14px;
            padding: 0 15px;
        }
        &__blockquote{
            padding: 20px 15px 30px;
            margin-bottom: 60px;
            blockquote{
                font-size: 20px;
                line-height: 30px;
                color: #4785EE;
            }
        }
    }
}

@media (max-width:576px) {
    .keys{
        padding-top: 0;
        overflow-x: hidden;
        &__container{
            max-width: 100%;
        }
        &__menu{
            padding: 12px 15px;
            border-top-left-radius: 0px;
            border-top-right-radius: 0px;
            margin-bottom: 30px;
        }
        &__text{
            padding: 0 15px;
        }
        &__logo{
            width: 100px;
            height: 35px;
        }
        &__phone a{
            font-size: 16px;
            line-height: 20px;
        }
        &__form{
            max-width: 100%;
        }
        &__button{
            width: 345px;
            padding: 21px 45px 21px 45px;
        }
        &__form{
            align-self: center;
        }
    }
}
