@media (max-width:1200px) {
    .autopark{
        &__container{
            max-width: 960px;
        }
        &__img{
            width: auto;
            height: auto;
        }
        &__button{
            flex-direction: column;
            min-width: 100%;
        }
        &__modal{
            margin-bottom: 35px;
        }
    }
}

@media (max-width:1024px) {
    .autopark{
        padding: 50px 0 52px 0;
        &__container{
            max-width: 720px;
        }
        &__img{
            display: none;
        }
        &__title{
            text-align: left;
        }
        &__block{
            justify-content: center;
        }
    }
}

@media (max-width:768px) {
    .autopark{
        &__container{
            max-width: 540px;
        }
        &__tabs{
            width: 100%;
            margin-bottom: 45px;
            
            overflow-x: scroll;
            white-space: nowrap;
            &::-webkit-scrollbar {
                width: 0;
            }
        }
        &__title{
            font-size: 27px;
        }
        &__item{
            margin-right: 29px;
        }
        &__block{
            justify-content: center;
        }
    }
}



@media (max-width:576px) {
    .autopark{
        overflow-x: hidden;
        &__img{
            display: none;
        }
        &__whatsapp{
            display: none;
        }
        &__container{
            max-width: 100%;
            padding: 0 15px;
            margin: 0 auto;
        }
        &__title{
            text-align: left;
            font-size: 27px;
            line-height: 36px;
            margin-bottom: 20px;
        }
        &__button{
            flex-direction: column;
            align-items: center;
            justify-content: center;
            max-width: 345px;
        }
        &__modal{
            min-width: 0;
            margin-bottom: 20px;
            width: 100%;
        }
        &__form{
            min-width: 0;
            width: 100%;
        }
    }
}