@media (max-width:1200px) {
    .comfort{
        &__container{
            max-width: 960px;
            margin: 0 auto;
        }
    }
}

@media (max-width:1024px) {
    .comfort{
        &__container{
            max-width: 720px;
            margin: 0 auto;
        }
    }
}

@media (max-width:768px) {
    .comfort{
        padding: 50px 0 60px 0;
        &__container{
            max-width: 540px;
            margin: 0 auto;
        }
        &__title{
            margin-bottom: 35px;
        }
        &__wrapper{
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
        &__block{
            max-width: 245px;
            min-height: 181px;
        }
        &__block:nth-child(-n+4){
            margin-bottom: 25px;
        }
        
    }
}

@media (max-width:576px) {
    .comfort{
        &__container{
            max-width: 100%;
            padding: 0 15px;
            margin: 0 auto;
        }
        &__title{
            text-align: left;
            font-size: 26px;
            line-height: 36px;
            max-width: 345px;
            margin-left: 0;
            margin-right: auto;
        }
        &__block{
            max-width: 345px;
        }
    }
}