@media (max-width: 1200px) {
    .move{
        &__container{
            max-width: 960px;
        }
    }
}

@media (max-width: 1024px) {
    .move{
        &__container{
            max-width: 720px;
        }
    }
}

@media (max-width: 768px) {
    .move{
        padding: 50px 0 70px 0;
        &__container{
            max-width: 540px;
        }
        .swiperTwo{
            display: none;
        }
        &__title{
            text-align: center;
            margin-bottom: 29px;
            font-size: 27px;
            line-height: 36px
        }
        &__mobile{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            margin: 0 auto;
        }
        &__block{
            max-width: 345px;
        }
        &__mobile__button{
            margin-top: 30px;
            padding: 21px 110px;
            border: 1px solid #4785EE;
            border-radius: 9px;
            background: transparent;
            font-family: 'Grava Display';
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 20px;
            color: #4785EE;
        }
    }
}

@media (max-width: 576px) {
    .move{
        &__container{
            max-width: 100%;
            padding: 0 15px;
        }
        &__wrapper{
            max-width: 100%;
            padding: 0 15px;
        }
        &__title{
            text-align: left;
        }
        &__mobile{
            justify-content: flex-start;
            align-items: flex-start;
        }
    }
}

@media (max-width:374px) {
    .move{
        &__mobile__button{
            padding: 21px 90px;
        }
    }
}