.clients{
    padding: 78px 0 120px 0;
    background: #F6F5FC;
    &__container{
        max-width: 1170px;
        margin: 0 auto;
    }
    &__title{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 47px;
        color: $text;
        margin-bottom: 60px;
        max-width: 340px;
    }
    &__wrapper{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    &__link{
        display: block;
        width: 543px;
        height: 310px;
        border-radius: 24px;
        overflow: hidden;
        padding: 20px 20px 101px 20px;
        transition: all 0.3s;
        &:hover{
            .clients__way{
                color: $main-color;
                border-color: $main-color;
            }
            .clients__descr{
                color: $main-color;
                border-color: $main-color;
            }
        }
        &:nth-child(-n+2){
            margin-bottom: 62px;
        }
        &_one{
            background: url('../img/clients/client-1.jpg') center center /cover no-repeat;
        }
        &_two{
            background: url('../img/clients/client-2.jpg') center center /cover no-repeat;
        }
        &_three{
            background: url('../img/clients/client-3.jpg') center center /cover no-repeat;
        }
        &_four{
            background: url('../img/clients/client-4.jpg') center center /cover no-repeat;
        }
    }

    &__way{
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: $white;
        padding: 9px 15px 9px 15px;
        border: 1px solid $white;
        margin-bottom: 55px;
        display: inline-block;
        transition: all 0.3s;
    }
    &__descr{
        max-width: 503px;
        font-family: 'Grava Display';
        font-style: normal;
        font-weight: 600;
        font-size: 27px;
        line-height: 36px;
        color: $white;
        transition: all 0.3s;
    }
}