@media (max-width:1200px) {
    .quiz{
        &__container{
            max-width: 960px;
        }
    }
}

@media (max-width:1024px) {
    .quiz{
        &__container{
            max-width: 720px;
        }
    }
}

@media (max-width:768px) {
    .quiz{
        padding: 54px 0 70px 0;
        &__container{
            max-width: 540px;
        }
        &__title{
            margin-right: 0;
            margin-left: 0;
            text-align: left;
        }
        &__subtitle{
            margin-right: 0;
            margin-left: 0;
            text-align: left;
        }
    }
}

@media (max-width:576px) {
    .quiz{
        &__container{
            max-width: 100%;
            padding: 0 15px;
        }
        &__title{
            font-size: 26px;
            line-height: 36px;
            max-width: 345px;
            margin: 0;
            margin-bottom: 15px;
            text-align: left;
            
        }
        &__subtitle{
            font-size: 20px;
            line-height: 30px;
            max-width: 345px;
            margin-bottom: 35px;
            text-align: left;
        }
        &__form{
            padding: 35px 15px 22px;
        }
        &__package{
            font-size: 24px;
            line-height: 30px;
        }
        .current{
            &:before{
                display: none;
            }
        }
    }
}

