* {
    box-sizing: border-box;
}
body{
    position: relative;
}
h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

a{
    text-decoration: none;
    transition: all 0.3s;
    &:hover{
        text-decoration: none;
    }
}
ul{
    list-style: none;
    text-decoration: none;
}
li{
    list-style: none;
    text-decoration: none;
}
p{
    margin: 0;
}
img{
    width: 100%;
}