.move{
    padding: 90px 0 137px 0;
    background-color: #F6F5FC;
    &__container{
        max-width: 1170px;
        margin: 0 auto;
    }
    &__title{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 47px;
        color: $text;
        margin-bottom: 60px;
        text-align: left;
    }
    &__wrapper{
        overflow-x: hidden;
        max-width: 1440px;
        margin: 0 auto;
    }
    .swiper {
        width: 100%;
        height: 883px;
    }
    .swiper-slide{
        max-width: 367px;
        transition: all 0.3s;
    }
    .swiper-wrapper{
        display: flex;
    }
    &__block{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        max-width: 367px;
        &:not(:last-child){
            margin-bottom: 32px;
        }
    }
    &__img{
        width: 100%;
        height: 100%;
    }
    &__text{
        width: 100%;
        margin-top: 14px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__data{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: $text;
        mix-blend-mode: normal;
        opacity: 0.64;
    }
    &__way{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        color: $text;
    }
    &__mobile{
        display: none;
    }
}