@media (max-width:1200px) {
    .clients{
        &__container{
            max-width: 960px;
            margin: 0 auto
        }
        &__title{
            margin-right: auto;
            margin-left: auto;
            max-width: 100%;
            text-align: center;
        }
        &__wrapper{
            justify-content: center;
        }
        .clients__link:nth-child(-n+3){
            margin-bottom: 62px;
        }
    }
}

@media (max-width:1024px) {
    .clients{
        &__container{
            max-width: 720px;
        }
        &__title{
            margin-bottom: 35px;
            text-align: left;
        }
    }
}

@media (max-width:768px) {
    .clients{
        &__container{
            max-width: 540px;
        }
        &__title{
            font-size: 27px;
            line-height: 36px;
        }
    }
}

@media (max-width:576px) {
    .clients{
        padding: 50px 0 70px 0;
        &__container{
            width: 100%;
            padding: 0 15px;
            margin: 0;
        }
        &__link{
            max-width: 375px;
            max-height: 240px;
        }
        &__descr{
            font-size: 20px;
            line-height: 30px;
        }
        &__way{
            margin-bottom: 35px;
        }
    }
}