.step{
    background: $bg;
    padding: 120px 0 164px 0;
    &__container{
        margin: 0 auto;
        max-width: 1170px;
    }
    &__wrapper{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__block{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        max-width: 360px;
    }
    &__number{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 30px;
        color: $white;
        margin-bottom: 4px;
    }
    &__descr{
        font-family: 'Grava';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 24px;
        color: $white;
        a{
            color: $white;
            border-bottom: 1px solid $white;
            &:hover{
                color: $text;
            }
        }
        
    }
}