@media (max-width:1200px) {
    .footer{
        &__container{
            max-width: 920px;
        }
    }
}

@media (max-width:1024px) {
    .footer{
        &__container{
            max-width: 720px;
        }
        &__button{
            display: none;
        }
    }
}

@media (max-width:768px) {
    .footer{
        &__container{
            max-width: 540px;
        }
    }
}

@media (max-width:576px) {
    .footer{
        padding: 40px 0 129px 0;
        &__container{
            max-width: 100%;
            padding: 0 15px;
        }
        &__contacts{
            flex-direction: column;
            margin-bottom: 35px;
            min-height: 165px;
            justify-content: space-between;
            
        }
        &__data{
            display: none;
        }
        &__policy{
            a{
                &:nth-child(1){
                    display: none;
                }
            }
        }
    }
}